<template>
  <div>
    <div class="page-title">
      <h3>Общая статистика отрядов</h3>

      <button class="btn waves-effect waves-light btn-small" @click="refresh">
        <i class="material-icons">refresh</i>
      </button>
    </div>
    <div class="row">
      <Loader v-if="loading"/>
      <v-table v-else :data='n'>
        <thead slot="head">
          <v-th sortKey="id">Место</v-th>
          <v-th sortKey="playerName" defaultSort="desc">Отряд</v-th>
          <v-th sortKey="kills" defaultSort="desc">Убийств</v-th>
          <v-th sortKey="teamkills" defaultSort="desc">Тимкиллов</v-th>
          <v-th sortKey="totalScore" defaultSort="desc">Счет</v-th>
          </thead>
          <tbody slot="body" slot-scope="{displayData}">
        <tr v-for="row in displayData" :key="row.id">
          <td>{{row.id}}</td>
          <td>{{row.prefix}}</td>
          <td>{{row.kills}}</td>
          <td>{{row.teamkills}}</td>
          <td>{{row.score}}</td>
        </tr>
          </tbody>
      </v-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "Squads",
  data: () => ({
    n: [],
    loading: false,
  }),
  async mounted() {
      this.loading = true;
      await this.r();
      this.loading = false;
  },
  methods: {
    async refresh() {
      this.loading = true;
      await this.r()
      this.loading = false;
    },
    async r(){
      this.n = [];
      let r = await fetch("stats.json");
      r = await r.json();
      let id = 1;
      for (let key in r.squadStatistics) {
         r.squadStatistics[key].id = id;
          id++;
          this.n.push(r.squadStatistics[key]);
      }
    }
  },
};
</script>
